const app = getApp()

export default {
	data() {
		return {
			keyword: '',
			scrollTop: 0,
			activityindex: 0,
			reload: false,
			cardCur: 0,
			more: true,
			swiperList: [],
			dotStyle: false,
			url: "/static/cardback0.jpg",
			activitylist: [],
			current: 0,
			tabs: [
				"介绍", "动态"
			]
		};
	},
	onLoad() {
		console.log(app.globalData.cookie)
		if (app.globalData.cookie != '') {
			console.log('indexonload')
			this.loadpage()
		} else {
			console.log('nocookie')
			app.globalData.cookieReadyCallback = this.loadpage
		}

		uni.showShareMenu({})

	},
	onPageScroll(res) {
		this.scrollTop = res.scrollTop
	},
	onPullDownRefresh() {
		this.more = true
		this.activityindex = 0
		this.loadpage()
	},
	onReachBottom() {
		this.loadactivity(false)
	},
	onShareAppMessage(res) {
		return {
			title: app.globalData.sharetitle,
			path: '/pages/index/index',
			imageUrl: app.globalData.shareimg
		}
	},
	methods: {
		loadpage() {
			uni.showLoading({
				title: '加载中'
			})
			uni.request({
				url: app.globalData.apiurl + 'users/broadcast',
				header: {
					'content-type': 'application/json', //自定义请求头信息
					'cookie': app.globalData.cookie
				},
				success: (res) => {
					this.swiperList = []
					this.reload = false
					console.log(res)
					console.log(res.data);
					this.swiperList = res.data.list
					this.reload = true
					if (this.activitylist != []) {
						uni.stopPullDownRefresh()
					}
				}
			})
			this.loadactivity(true)
		},
		loadactivity(reload) {
			if (this.more) {
				uni.request({
					url: app.globalData.apiurl + 'users/index',
					data: {
						index: this.activityindex
					},
					header: {
						'content-type': 'application/json', //自定义请求头信息
						'cookie': app.globalData.cookie
					},
					success: (res) => {
						console.log(res)
						console.log(res.data)
						if (res.data.events) {
							res.data.events.forEach((res, index) => {
								//res.like = true
								res.delay = '' + (index + 5) * 0.1 + 's'
								setTimeout(() => {
									res.delay = undefined
								}, (index + 11) * 100)
							})
							if (reload) {
								this.activitylist = []
								uni.showToast({
									title: "加载成功",
									icon: 'none'
								})
							} else {
								setTimeout(() => {
									uni.pageScrollTo({
										scrollTop: this.scrollTop + 300,
										duration: 500,
									})
									console.log("top" + this.scrollTop)
								}, 200)
							}
							this.activitylist = this.activitylist.concat(res.data.events)
							this.activityindex += res.data.events.length
							this.more = res.data.more
							console.log(this.activitylist)
						} else {
							uni.showToast({
								title: "加载失败",
								icon: 'none'
							})
						}
						if (this.swiperList != []) {
							uni.stopPullDownRefresh()
						}
					}
				})
			}
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		swiperActivity(e) {
			this.activityPage(this.swiperList[this.cardCur].event_id)
		},
		activityPage(id) {
			uni.navigateTo({
				url: "../activity/activity?id=" + id,
			})
		},
		InputBlur(e) {
			console.log(e)
		},
		searchPage(){
			console.log(this.keyword)
			uni.navigateTo({
				url: "../search/search?keyword=" + this.keyword
			})
		},
		like(index) {
			uni.request({
				url: app.globalData.apiurl + 'users/like/' + this.activitylist[index].event_id,
				method: 'POST',
				header: {
					'content-type': 'application/json', //自定义请求头信息
					'cookie': app.globalData.cookie
				},
				success: (res) => {
					console.log(res.data)
					console.log(index)
					this.activitylist[index].like = res.data.like
				}
			});
		}
	}
}
